@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --primaryColor : #5fb7ea;
  --primaryRGBA : rgba(0,0,0,0.5);
  --mainGrey: #fafafa;
  --darkGrey: #5f5e5e;
  --mainWhite: #fff;
  --mainBlack: #222;
  --mainTransition : 0.3s all ease-in-out;
  --mainSpacing : 0.3rem
}

body{
  font-family: 'Open Sans', sans-serif !important;
}

.text-main {
  font-family: 'Montserrat', sans-serif;
  color : red;
}
.cart{
  margin-right : 200px;
  position: relative;
  cursor: pointer;
}
.cart-link{
  font-size: 1.5rem;
}
.cart > span{
  position: absolute;
  top: -12px;
  left: 15px;
  background: #5fb7ea;
  background: var(--primaryColor);
  padding: 1px 3px;
  border-radius: 50%;
}
.img-container{
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 350px;
  -webkit-transition: 1.5s;
  transition: 1.5s;
}

.imgs{
  cursor: pointer;
  -webkit-transition: 1.5s;
  transition: 1.5s;
  background: #ccc;
  width: 100%;
}
.imgs:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.but{
  background: #000;
  color: #fff;
  padding: 12px;
  display: inline-block;
  position: absolute;
  bottom: 0;
}
.but:hover{
  text-decoration: none;
  color: #ccc;
}
.search{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  font-size: 50px;
  cursor: pointer;
  color: #fff;
  -webkit-transition:1.5s;
  transition:1.5s;
  opacity: 0;
}
.img-container:hover .imgs{
  opacity: .3;
}
.img-container:hover .search{
  opacity: 1;
}

.icon{
  font-size: 1rem;
  background: rgba(0,0,0,0.5);
  background: var(--primaryRGBA);
  margin: 1rem 1rem 1rem 0;
  color:#ccc;
  background: #000;
  border-radius: 0.5rem;
  padding: 0.5rem;
  cursor: pointer;
  width: 400px;
  outline: none;
}
.icons-item{
  cursor: pointer;
}
